* {
  margin: 0;
  padding: 0;
}
.body {
  font-family: "Baloo 2", cursive;
}
.root_heading {
  background: rgba(43, 35, 199, 0.75);
  width: 100vw;
  height: 14vh;
}

.root_heading--title {
  letter-spacing: 0.5px;
  text-transform: uppercase;
  font-size: 22px;
  font-family: Roboto, sans-serif;
  color: white;
  padding-top: 70px;
  text-align: center;
  margin: 0px 15px 0 15px;
}

.card {
  width: 100vw;
  height: 64vw;
  display: flex;
  flex-direction: column;
}

.root_notice {
  border: 1px solid black;
  width: 90vw;
  margin: 2rem auto;
  -moz-box-shadow: inset 0 0 10px #000000;
  -webkit-box-shadow: inset 0 0 10px #000000;
  box-shadow: inset 0 0 10px #000000;
  border-radius: 3%;
  padding: 15px;
}
.root_notice_text {
  padding: 0 15px 0 15px;
  font-size: 19px;
}

.root_notice_title {
  padding: 0 15px 0 15px;
}

a {
  color: black;
  text-decoration: none;
}
button.card_start_button {
  border: 1px black;
  border-radius: 50px;
  padding: 15px;
  width: 35%;
  margin: 0 auto;
  background: rgba(43, 35, 199, 0.75);
}

p.card_tool_text {
  padding: 0 15px 0 15px;
  line-height: 1.3;
  font-size: 19px;
  margin-bottom: 2rem;
}

.card_tool_text--exposed {
  background: rgba(82, 82, 82, 0.38);
}

.card_tool_text_link {
  text-decoration: underline;
  color: #36547c;
  font-size: 19px;
}

.card_start {
  font-size: 19px;
}

/* footer */

.page_footer_container {
  background: rgba(43, 35, 199, 0.75);
  width: 100vw;
  height: auto;
  padding: 10px;
  padding: 0 15px 0 15px;
}

p.page_footer_text {
  font-size: 16px;
  color: white;
}

.hide {
  display: none;
}

.active {
  display: block;
}

.active_flex {
  display: flex;
}

.card__form {
  border: 1px solid black;
  width: auto;
  height: auto;
  margin: 2rem 0;
  padding: 20px;
  border-radius: 30px;
}

/* form start */

.form__container {
  display: none;
  flex-direction: column;
  border: 1px solid black;
  align-items: flex-start;
  margin: 0 80px 0 80px;
  padding: 10px;
  border-radius: 30px;
}

.form {
  border: 1px solid black;
  padding: 5px;
  margin: 10px;
  display: flex;
  width: 150px;
}

.form__reply--ending {
  border: 1px black dotted;
  border-radius: 50px;
  padding: 15px;
}

/*     page 2 contents */

.page-content {
  padding: 0 40px 0 40px;
  line-height: 1.4;
  letter-spacing: 0.3px;
}

h1 {
  color: #335d9f;
  font-weight: 300;
  font-size: 30px;
  margin-bottom: 15px;
  padding-top: 57px;
}

html {
  font-family: "Open Sans";
  color: #545454;
  font-weight: 300;
  font-size: 18px;
}

p {
  margin: 0 0 10px;
}
body p {
  line-height: 28px;
}
li {
  margin: 20px;
}

.page_entry_paragraph {
  margin-bottom: 15px;
}

.page_link--reference {
  color: #16acce;
}
